/***************************Animations******************************/

@-webkit-keyframes slideInLeft {
  from {
    visibility: visible;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes slideInLeft {
  from {
    visibility: visible;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes slideOutRight {
  from {
    visibility: visible;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  to {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

@keyframes slideOutRight {
  from {
    visibility: visible;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  to {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

@-webkit-keyframes slideInTop {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
  }

  to {
    visibility: visible;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes slideInTop {
  from {
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
  }

  to {
    visibility: visible;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes slideOutDown {
  from {
    visibility: visible;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
  }
}

@keyframes slideOutDown {
  from {
    visibility: visible;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
  }
}

@-webkit-keyframes scaleIn {
  from {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }

  to {
    visibility: visible;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes scaleIn {
  from {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }

  to {
    visibility: visible;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes scaleOut {
  from {
    visibility: visible;
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  to {
    visibility: hidden;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}

@keyframes scaleOut {
  from {
    visibility: visible;
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  to {
    visibility: hidden;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}



.has-open-lhmodal,
.has-open-lhmodal body {
  overflow: hidden;
  height: 100%;
  position: relative;
}

.modal {
  position: fixed;
  z-index: 2;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  font-size: 0;
  visibility: hidden;
  background: rgba(0, 0, 0, 0.5);
  -webkit-transition: all ease-out 0.5s;
  -o-transition: all ease-out 0.5s;
  transition: all ease-out 0.5s;
  text-align: center;
  overflow-y: auto;
  overflow-x: hidden;
  opacity: 0;
}

.modal::after {
  content: '';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.modal_in {
  opacity: 1;
  visibility: visible;
}

.modal__content {
  position: relative;
  display: inline-block;

  width: 90%;
  max-width: 480px;
  max-width: 30rem;
  margin: 48px 0;
  margin: 3rem 0;
  padding: 16px;
  padding: 1rem;

  -webkit-animation-duration: 0.2s;

          animation-duration: 0.2s;
  text-align: left;
  vertical-align: middle;

  background-color: #fff;

  -webkit-animation-fill-mode: both;

          animation-fill-mode: both;
}

.modal_in .modal__content {
  -webkit-animation-name: scaleIn;
          animation-name: scaleIn;
}

.modal_out .modal__content {
  -webkit-animation-name: scaleOut;
          animation-name: scaleOut;
}

.modal__close {

  position: absolute;
  top: 4.8px;
  top: 0.3rem;
  right: 8px;
  right: 0.5rem;

  padding: 0;

  cursor: pointer;

  opacity: 0.6;
  border: none;
  background: none;
  font-size: 16px;
  font-size: 1rem;
}

.modal__close:hover {
  opacity: 1;
}

.modal__close:active,
.modal__close:focus {
  outline: none;
}
