  background: #fff;
}
.page-content {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  overflow: hidden;
  color: #626161;
}
.wrapper {
  width: 980px;
  margin: 0 auto;
}
.module-name {
  font-family: "Intro-Regular-Alt";
