  text-align: center;
  color: #626161;
  margin: 34px 0;
  line-height: 120%;
}
.block_blue {
  background: -webkit-gradient(linear, left bottom, left top, from(#21d0e2), to(#1bbbd8));
  background: -o-linear-gradient(bottom, #21d0e2, #1bbbd8);
  background: linear-gradient(to top, #21d0e2, #1bbbd8);
  color: #fff;
}
.flex-row {
