.walk-men {
  height: 225px;
  position: relative;
  background: -o-radial-gradient(center, ellipse closest-side, #d6e3e5, #ffffff);
  background: radial-gradient(ellipse closest-side at center, #d6e3e5, #ffffff);
  padding-top: 10px;
}
.walk-men__image {
  position: absolute;
  left: 0;
  bottom: -35px;
  height: 220px;
  width: auto;
  -webkit-animation: walk-man 15s infinite;
          animation: walk-man 15s infinite;
  -webkit-transform: rotateZ(0deg);
      -ms-transform: rotate(0deg);
          transform: rotateZ(0deg);
}
@-webkit-keyframes walk-man {
  from {
    left: 0;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-transform: translateX(0) rotateY(0deg);
            transform: translateX(0) rotateY(0deg);
  }
  49% {
    left: 100%;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-transform: translateX(-100%) rotateY(0deg);
            transform: translateX(-100%) rotateY(0deg);
  }
  50% {
    left: 100%;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-transform: translateX(-100%) rotateY(180deg);
            transform: translateX(-100%) rotateY(180deg);
  }
  99% {
    left: 0;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-transform: translateX(0) rotateY(180deg);
            transform: translateX(0) rotateY(180deg);
  }
  to {
    left: 0;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-transform: translateX(0) rotateY(0deg);
            transform: translateX(0) rotateY(0deg);
  }
}
@keyframes walk-man {
  from {
    left: 0;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-transform: translateX(0) rotateY(0deg);
            transform: translateX(0) rotateY(0deg);
  }
  49% {
    left: 100%;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-transform: translateX(-100%) rotateY(0deg);
            transform: translateX(-100%) rotateY(0deg);
  }
  50% {
    left: 100%;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-transform: translateX(-100%) rotateY(180deg);
            transform: translateX(-100%) rotateY(180deg);
  }
  99% {
    left: 0;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-transform: translateX(0) rotateY(180deg);
            transform: translateX(0) rotateY(180deg);
  }
  to {
    left: 0;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-transform: translateX(0) rotateY(0deg);
            transform: translateX(0) rotateY(0deg);
  }
}
.walk-men-wrapper {
  text-align: center;
}
.walk-men__counter {
  text-align: left;
  min-width: 370px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 8px;
  background: #fff;
  margin: 0 auto;
  position: relative;
  overflow: visible;
  display: inline-block;
}
.walk-men__counter-info {
  padding: 25px 30px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 4px solid #d3eff2;
  font-family: OpenSans-Regular;
  font-size: 14px;
  position: relative;
}
.walk-men__counter-text_big {
  font-size: 20px;
  font-family: Intro-Regular;
}
.walk-men__counter-text_blue {
  color: #1bbbd9;
  font-family: Intro-Bold-Alt;
}
.walk-men-counter__leg {
  position: absolute;
  top: 100%;
  width: 10px;
  height: 100%;
  background: -webkit-gradient(linear, left top, right top, from(#abbbbd), to(#c4d4d6));
  background: -o-linear-gradient(left, #abbbbd 0%, #c4d4d6 100%);
  background: linear-gradient(to right, #abbbbd 0%, #c4d4d6 100%);
  content: '';
}
.walk-men-counter__leg_left {
  left: 50px;
}
.walk-men-counter__leg_right {
  right: 50px;
}