.events {
  background: url(../images/events/background-winter.jpg) no-repeat center bottom;
  min-height: 450px;
  margin-bottom: 10px;
  position: relative;
}
.events-wrapper {
  padding: 8px 0;
}
.event-items {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.event-item {
  padding: 30px 0;
  height: 270px;
  margin-left: -15px;
  width: 245px;
  color: #ffffff;
  position: relative;
  text-align: center;
}
.event-item:nth-child(1) {
  top: 40px;
}
.event-item:nth-child(2) {
  top: 20px;
}
.flag-rectangle__1 {
  position: relative;
  background: #c63e61;
  -webkit-transform: skewY(10deg);
      -ms-transform: skewY(10deg);
          transform: skewY(10deg);
  width: 236px;
  height: 55px;
}
.flag-rectangle__2 {
  position: absolute;
  left: 100%;
  top: -7px;
  background: #c63e61;
  -webkit-transform: skewY(-60deg);
      -ms-transform: skewY(-60deg);
          transform: skewY(-60deg);
  width: 8px;
  height: 55px;
}
.flag__leg {
  position: absolute;
  top: 0;
  width: 4px;
  height: 260px;
  background: #d0d0d0;
  z-index: 1;
}
.flag__leg_left {
  left: 20px;
}
.flag__leg_right {
  right: 0;
}
.flag-rectangle__3 {
  position: absolute;
  left: calc(100% + 8px);
  top: -85px;
  background: #de476e;
  -webkit-transform: skewY(-20deg) translateX(-100%);
      -ms-transform: skewY(-20deg) translateX(-100%);
          transform: skewY(-20deg) translateX(-100%);
  width: 230px;
  height: 86px;
  z-index: 4;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-family: Intro-Regular;
  font-size: 18px;
  color: #ffffff;
  text-decoration: none;
}
.flag-rectangle__3 span {
  border-bottom: 2px solid transparent;
  padding: 7px 0;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
.flag-rectangle__3:hover span {
  border-bottom: 2px solid #fff;
  padding: 0;
}
.flag-rectangle__4 {
  position: absolute;
  left: 0;
  top: -10px;
  background: #f2537c;
  -webkit-transform: skewY(-20deg);
      -ms-transform: skewY(-20deg);
          transform: skewY(-20deg);
  min-width: 55px;
  height: 55px;
  z-index: 5;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 30px;
  font-family: Intro-Bold-Alt;
}
.skier {
  position: absolute;
  width: 192px;
  height: 130px;
  background: url(../icons/6ed7c3190bd7c9d2813152c7923917e8.png) no-repeat center center;
  -webkit-animation: skier 6s infinite;
          animation: skier 6s infinite;
  right: -200px;
}
@-webkit-keyframes skier {
  from {
    right: -200px;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    bottom: 70px;
  }
  80% {
    right: 100%;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    bottom: 0;
  }
  to {
    right: 100%;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    bottom: 0;
  }
}
@keyframes skier {
  from {
    right: -200px;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    bottom: 70px;
  }
  80% {
    right: 100%;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    bottom: 0;
  }
  to {
    right: 100%;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    bottom: 0;
  }
}